import React from "react";
import { Button, Message, Container } from "./style";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faComment, faAngleUp } from "@fortawesome/free-solid-svg-icons";
import { useState, useEffect } from "react";

function QuickButtons({ modalOpen, setModalOpen }) {
  const [ScrollY, setScrollY] = useState(0);
  const [BtnStatus, setBtnStatus] = useState(false); //  

  const handleFollow = () => {
    setScrollY(window.pageYOffset);
    if (ScrollY > 100) {
      // 100   
      setBtnStatus(true);
    } else {
      // 100   
      setBtnStatus(false);
    }
  };

  const handleTop = () => {
    //     
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setScrollY(0); // ScrollY   
    setBtnStatus(false); // BtnStatus  false  =>  
  };

  const handleCmt = () => {
    window.scrollTo({
      // top: 0,
      behavior: "smooth",
    });
  };

  useEffect(() => {
    const watch = () => {
      window.addEventListener("scroll", handleFollow);
    };
    watch();
    return () => {
      window.removeEventListener("scroll", handleFollow);
    };
  });

  return (
    <Container>
      <Button
        className={BtnStatus ? "topBtn active" : "topBtn"}
        onClick={() => {
          // setModalOpen(!modalOpen);
          handleTop();
        }}
      >
        <Message>
          <FontAwesomeIcon icon={faAngleUp} fontSize={"36px"} />
        </Message>
      </Button>
      {/* <Button
        className={"cmtBtn"}
        onClick={() => {
          handleCmt();
        }}
      >
        <Message>
          <FontAwesomeIcon icon={faComment} fontSize={"24px"} />
        </Message>
      </Button> */}
    </Container>
  );
}

export default QuickButtons;
